      <el-table
        :data="listData"
        size="small"
        style="width: 100%;top:10px;"
        border
        :header-cell-style="{
          'background-color': '#e0ecf9',
          color: '#3870af '
        }"
      >
        <el-table-column prop="deptNo" width="50"> </el-table-column>
        <el-table-column prop="deptName" label="" width="180">
        </el-table-column>
        <el-table-column prop="deptName" label="" width="180">
        </el-table-column>
        <el-table-column prop="deptName" label="" width="180">
        </el-table-column>
