      <el-table-column sortable prop="deptName" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="deptNo" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="editTime" label="" width="300">
        <template slot-scope="scope">
          <div>{{ scope.row.editTime | timestampToTime }}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="editUser" label="" width="300">
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="deleteUser(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table> -->
    <!--  -->
    <Pagination
      v-bind:child-msg="pageparm"
      @callFather="callFather"
    ></Pagination>
    <!--  -->
    <el-dialog
      :title="title"
      :visible.sync="editFormVisible"
      width="30%"
      @click="closeDialog"
    >
      <el-form
        label-width="120px"
        :model="editForm"
        :rules="rules"
        ref="editForm"
      >
        <el-form-item label="" prop="deptName">
          <el-input
            size="small"
            v-model="editForm.deptName"
            auto-complete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="deptNo">
          <el-input
            size="small"
            v-model="editForm.deptNo"
