<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <div>
      <el-container
        style="height: 500px; border: 1px solid #eee;margin-top: 20px"
      >
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu>
            <el-tree
              :data="data"
              :props="defaultProps"
              @node-click="handleNodeClick"
            ></el-tree>
          </el-menu>
        </el-aside>
  
        <el-container style="margin-top: 5px;margin-left: 5px">
          <el-menu>
            <el-button type="primary" icon="el-icon-circle-close" size="mini"
              ></el-button
            >
            <el-button type="primary" icon="el-icon-document" size="mini"
              ></el-button
            >

            <el-form
              ref="ruleForm"
              :rules="rules"
              :inline="true"
              :model="ruleForm"
              class="user-search"
              label-width="140px"
            >
              <el-form-item label="" prop="name">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="name">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="name">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="name">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="name">
                <el-select
                  v-model="ruleForm.name"
                  size="small"
                  style="width:190px"
                >
                  <el-option label="" value="shanghai"></el-option>
                  <el-option label="" value="beijing"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input size="small" v-model="ruleForm.name"></el-input>
              </el-form-item>
            </el-form>
          </el-menu>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [
        {
          label: " 1",
          children: [
            {
              label: " 1-1",
              children: [
                {
                  label: " 1-1-1"
                }
              ]
            }
          ]
        },
        {
          label: " 2",
          children: [
            {
              label: " 2-1",
              children: [
                {
                  label: " 2-1-1"
                }
              ]
            },
            {
              label: " 2-2",
              children: [
                {
                  label: " 2-2-1"
                }
              ]
            }
          ]
        },
        {
          label: " 3",
          children: [
            {
              label: " 3-1",
              children: [
                {
                  label: " 3-1-1"
                }
              ]
            },
            {
              label: " 3-2",
              children: [
                {
                  label: " 3-2-1"
                }
              ]
            }
          ]
        }
      ],
      ruleForm: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        region: [
          { required: true, message: "", trigger: "change" }
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        resource: [
          { required: true, message: "", trigger: "change" }
        ],
        desc: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  }
};
</script>

<style>
.user-search {
  margin-top: 10px;
}
</style>



