/** *   */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-container>
      <el-card class="box-card">
        <el-form
          :inline="true"
          :model="formInline"
          class="demo-form-inline"
          style="margin-top:10px"
        >
          <el-form-item size="mini">
            <el-input v-model="formInline.user" placeholder=""></el-input>
          </el-form-item>
          <el-form-item size="mini">
            <el-input v-model="formInline.region" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button icon="el-icon-search" @click="onSubmit" size="mini"
              ></el-button
            >
          </el-form-item>
          <el-row :gutter="20">
            <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-button type="success" size="mini" icon="el-icon-plus"
                  ></el-button
                >
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-button type="primary" size="mini" icon="el-icon-edit"
                  ></el-button
                >
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-button type="danger" size="mini" icon="el-icon-delete"
                  ></el-button
                >
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-button
                  type="info"
                  size="mini"
                  icon="el-icon-upload2
"
                  ></el-button
                >
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-button
                  type="warning"
                  size="mini"
                  icon="el-icon-download
"
                  ></el-button
                >
              </div></el-col
            >
          </el-row>
          <el-table
            border
            :data="tableData"
            style="width: 100%;margin-top:10px"
            :default-sort="{ prop: 'date', order: 'descending' }"
          >
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column prop="date" label="" sortable width="100">
            </el-table-column>
            <el-table-column prop="name" label="  " sortable width="100">
            </el-table-column>
            <el-table-column prop="name" label="" sortable width="100">
            </el-table-column>
            <el-table-column prop="name" label="" sortable width="150">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleEdit(scope.$index, scope.row)"
                  icon="el-icon-edit"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"
                  icon="el-icon-delete"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form>
      </el-card>
      <!---->

      <el-container style=" margin-left:100px">
        <el-card>
          <el-row :gutter="340">
            <el-col :span="30"
              ><div class="grid-content bg-purple"></div></el-col
            >
            <el-col :span="60"
              ><div class="grid-content bg-purple-light">
                <el-button type="primary" icon="el-icon-plus" size="mini"
                  ></el-button
                >
              </div></el-col
            >
            <el-col :span="50">
              <el-table
                border
                :data="tableData"
                style="width: 100%"
                :default-sort="{ prop: 'date', order: 'descending' }"
              >
                <el-table-column prop="date" label="" sortable width="100">
                </el-table-column>
                <el-table-column prop="date" label="" sortable width="100">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="  "
                  sortable
                  width="100"
                >
                </el-table-column>
                <el-table-column prop="name" label="" sortable width="100">
                </el-table-column>
                <el-table-column prop="name" label="" sortable width="150">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      type="primary"
                      @click="handleEdit(scope.$index, scope.row)"
                      icon="el-icon-edit"
                    ></el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDelete(scope.$index, scope.row)"
                      icon="el-icon-delete"
                    ></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-col>
          </el-row>
        </el-card>
      </el-container>
    </el-container>
    <!--  -->
    <Pagination
      v-bind:child-msg="pageparm"
      @callFather="callFather"
    ></Pagination>
    <!--  -->
  </div>
</template>

<script>
import { variableList, variableSave, variableDelete } from "../../api/userMG";
import Pagination from "../../components/Pagination";
export default {
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1517 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1519 "
        },
        {
          date: "2016-05-03",
          name: "",
          address: " 1516 "
        }
      ],
      formInline: {
        user: "",
        region: ""
      },
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: "",
      editForm: {
        varId: "",
        varLable: "",
        varName: "",
        varValue: "",
        token: localStorage.getItem("logintoken")
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: "",
        varName: "",
        token: localStorage.getItem("logintoken")
      },
      // 
      seletedata: {
        ids: "",
        token: localStorage.getItem("logintoken")
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    };
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  watch: {},

  /**
   * 
   */

  created() {
    this.getdata(this.formInline);
  },

  /**
   * 
   */

  methods: {
    // 
    getdata(parameter) {
      this.loading = true;
      // 
      let res = {
        code: 0,
        msg: null,
        count: 6,
        data: [
          {
            addUser: "root",
            editUser: "root",
            addTime: 1519385529000,
            editTime: 1520619747000,
            varId: 190,
            deptId: 1,
            varLable: "",
            varName: "domainName",
            varValue: "http://127.0.0.1:8080/iot-service-pay"
          },
          {
            addUser: "root",
            editUser: "root",
            addTime: 1519451541000,
            editTime: 1519451547000,
            varId: 191,
            deptId: 1,
            varLable: "",
            varName: "productImgURL",
            varValue: "http://ymwlw.com/pic"
          },
          {
            addUser: "root",
            editUser: "root",
            addTime: 1519452658000,
            editTime: 1519452661000,
            varId: 192,
            deptId: 1,
            varLable: "",
            varName: "groupDomain",
            varValue: "http://ymwlwl.com"
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1519879624000,
            editTime: 1519879628000,
            varId: 193,
            deptId: 1,
            varLable: "",
            varName: "envType",
            varValue: "false"
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1526374014000,
            editTime: 1526374031000,
            varId: 198,
            deptId: null,
            varLable: "sgh",
            varName: "jhjj",
            varValue: "jhjhgs"
          },
          {
            addUser: null,
            editUser: null,
            addTime: 1526453243000,
            editTime: 1526453243000,
            varId: 199,
            deptId: null,
            varLable: "v",
            varName: "v",
            varValue: "v"
          }
        ]
      };
      this.loading = false;
      this.listData = res.data;
      // 
      this.pageparm.currentPage = this.formInline.page;
      this.pageparm.pageSize = this.formInline.limit;
      this.pageparm.total = res.count;
      // 

      /***
       *  
       */
      // variableList(parameter)
      //   .then(res => {
      //     console.log(JSON.stringify(res))
      //     this.loading = false
      //     if (res.success == false) {
      //       this.$message({
      //         type: 'info',
      //         message: res.msg
      //       })
      //     } else {
      //       this.listData = res.data
      //       // 
      //       this.pageparm.currentPage = this.formInline.page
      //       this.pageparm.pageSize = this.formInline.limit
      //       this.pageparm.total = res.count
      //     }
      //   })
      //   .catch(err => {
      //     this.loading = false
      //     this.$message.error('')
      //   })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage;
      this.formInline.limit = parm.pageSize;
      this.getdata(this.formInline);
    },
    // 
    search() {
      this.getdata(this.formInline);
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      if (row != undefined && row != "undefined") {
        this.title = "";
        this.editForm.varId = row.varId;
        this.editForm.varLable = row.varLable;
        this.editForm.varName = row.varName;
        this.editForm.varValue = row.varValue;
      } else {
        this.title = "";
        this.editForm.varId = "";
        this.editForm.varLable = "";
        this.editForm.varName = "";
        this.editForm.varValue = "";
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          variableSave(this.editForm)
            .then(res => {
              this.editFormVisible = false;
              this.loading = false;
              if (res.success) {
                this.getdata(this.formInline);
                this.$message({
                  type: "success",
                  message: ""
                });
              } else {
                this.$message({
                  type: "info",
                  message: res.msg
                });
              }
            })
            .catch(err => {
              this.editFormVisible = false;
              this.loading = false;
              this.$message.error("");
            });
        } else {
          return false;
        }
      });
    },
    // 
    deleteUser(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          variableDelete(row.varId)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: "success",
                  message: ""
                });
                this.getdata(this.formInline);
              } else {
                this.$message({
                  type: "info",
                  message: res.msg
                });
              }
            })
            .catch(err => {
              this.loading = false;
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    closeDialog() {
      this.editFormVisible = false;
    }
  }
};
</script>

<style scoped>
.box-card {
  width: 1200px;
  margin-top: 10px;
}
</style>



